bizagi.bpmn.modeler.observable.extend("bizagi.bpmn.modeler.drawing", {}, {

    /* Constructor */
    init: function(model) {
        var that = this;
        this.canvas = null;
        $.templates({
            bpm_modeler_drawing: '<drawing> \
                                    {{for tasks tmpl="bpm_modeler_drawingItem"/}} \
                                  </drawing>',
            bpm_modeler_drawingItem: '<item id="{{: id}}" class="bpm-{{:type}} bpm-moveable" title="{{:displayName}}" \
                                            style="left:{{:position.x}}px; top:{{:position.y}}px" \
                                            data-type="{{: type}}"\
                                            data-show-aura="{{:showAura}}"> \
                                            <label class="labelNameTask label-{{:type}}">{{:displayName}}</label>\
                                            {{if type=="IntermediateEvent"}}<svg id="Layer_1" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><defs><style>.cls-1{fill:#fdffe7;}.cls-2{fill:#99a805;}</style></defs><title>event</title><circle class="cls-1" cx="12" cy="12" r="12"/><path class="cls-2" d="M12,1A11,11,0,1,1,1,12,11,11,0,0,1,12,1m0-1A12,12,0,1,0,24,12,12,12,0,0,0,12,0Z"/><path class="cls-2" d="M12,2.85A9.15,9.15,0,1,1,2.85,12,9.15,9.15,0,0,1,12,2.85m0-1.1A10.25,10.25,0,1,0,22.25,12,10.25,10.25,0,0,0,12,1.75Z"/></svg>{{/if}}\
                                            {{if type ==="EmailTask"}}<i class="ico_mail bpmn-icon-intermediate-event-throw-escalation"></i>{{/if}}\
                                            {{if ~root.isGateway(type)}}<gateway-helper></gateway-helper>{{/if}} \
                                            {{if type !="EmailTask" && durationInMinutes }}<duration>{{:durationInMinutes}}</duration>{{/if}} \
                                       </item>'
        });

        // Set model
        this.drawingWidth = 0;
        this.drawingHeight = 0;
        this.model = model;
        this.model.subscribe("model.redraw", $.proxy(this.refresh, this));
        this.previusElement = null;

        this.isEditionActive = false;

        this.reviewMode = false;

        // Create dependencies
        this.aura = new bizagi.bpmn.modeler.aura(this.model);
        this.aura.subscribe("item.drag", $.proxy(this.onElementDragging, this));
        this.aura.subscribe("arrow.drag", $.proxy(this.onArrowDragging, this));
        this.aura.subscribe("arrow.setExpression", $.proxy(this.editExpressionForArrow, this));
        this.aura.subscribe("arrow.setElse", $.proxy(this.triggerElseExpressionForArrow, this));
        this.aura.subscribe("task.externaloption", $.proxy(this.manageExternalOption, this));
        this.aura.subscribe("task.click", $.proxy(this.clickOnTask, this));
        this.connector = new bizagi.bpmn.modeler.connector();
        this.aux = "";
    },
    setCanvas: function(canvas) {
        this.canvas = canvas;
    },
    render: function(canvas, readonly) {
        var that = this;
        var initializeGeneralHandlers = (this.canvas == null);
        this.canvas = canvas;
        // Render template
        var viewModel = this.model.getViewModel();
        var html = $.templates.bpm_modeler_drawing.render(viewModel);

        canvas.append(html);
        // Set drawing canvas
        this.drawing = canvas.find("drawing");
        // Calculate size for drawing
        this.canvasWidth = canvas.width();
        this.canvasHeight = canvas.height();
        this.scrollTop = viewModel.canvas.top || 0;
        this.scrollLeft = viewModel.canvas.left || 0;
        this.drawingWidth = this.drawingWidth || viewModel.canvas.width || this.canvasWidth;
        this.drawingHeight = this.drawingHeight || viewModel.canvas.height || this.canvasHeight;
        //this.drawing.css({ minWidth: this.canvasWidth, minHeight: this.canvasHeight, width: this.drawingWidth, height: this.drawingHeight});
        this.drawing.width(this.drawingWidth);
        this.drawing.height(this.drawingHeight);
        this.canvas.scrollLeft(this.scrollLeft);
        this.canvas.scrollTop(this.scrollTop);

        // Add connectors
        setTimeout(function() { that.addConnectors(viewModel); }, 0);

        // Add event handlers
        if (readonly !== true && that.readonlyLoaded!==true) {
            this.addHandlers();
        }else{
            that.readonlyLoaded=true;
        }

        // Add general event handlers (ONLY ONCE)
        if (initializeGeneralHandlers && readonly !== true) {
            this.addGeneralHandlers();
        }

        // Show aura for item where data-show-aura = true
        this.drawing.find("item[data-show-aura=true]").click();

        // Set Name of all items
        this.configureNamesOfItems();

        //Set events in the document
        this.setGeneralEvents();
    },
    activeReviewMode: function() {
        $(document).find('#bpm-toolbar').hide();
        $(this.drawing).find('.bpm-moveable').draggable('disable');
        $(this.drawing).find('aura').hide();
        this.reviewMode = true;
        $("body").unbind('keyup');
        $("body").off();
    },
    setGeneralEvents: function() {
        var that = this;
        $("body").off();
        $("body").on("keyup", function(e) {
            if (!this.reviewMode) that.keyUpEvent(e);
        });
    },

    keyUpEvent: function(e) {
        var that = this
        var selectedItem = that.returnSelectedItem();
        if (selectedItem) {
            switch (e.which) {
                case 113: //F2 Change name of items
                    if (selectedItem.nodeName == "CONNECTOR") {
                        if (!that.isEditionActive) that.editConnectorName($(selectedItem.firstElementChild), "f2");
                    } else {

                        if (!that.isEditionActive) {
                            that.editTaskName($(selectedItem.firstElementChild), "f2");
                        }
                    }
                    break;
                case 46: //SUPR - Delete items
                    if (!this.reviewMode) {
                        if (selectedItem && selectedItem.nodeName == "CONNECTOR") {
                            var source = selectedItem.id.split("_to_")[0].split("svgFrom_")[1];
                            var target = selectedItem.id.split("_to_")[1];
                            that.model.deleteArrow(source, target);
                        } else {
                            var element = that.model.getTaskById(selectedItem.id);

                            if (that.model.activeConfirmationPopup(element.type)) {
                                var source = $("#" + selectedItem.id).parent().find(".bpm-option-delete")
                                that.aura.activePopupConfirmationDelete($(source), selectedItem.id);
                            } else {
                                that.model.deleteTask(element.id);
                            }
                        }

                    }
                    break;
            }
        }
    },

    configureNamesOfItems: function() {
        var that = this;
        $("#bpm-drawing").find(".labelNameTask").each(function(index, obj) {
            var value = $(obj).text();
            if (that.setNameTask(value, $(obj))) {
                $(obj).attr("desc", value);
                $(obj).text(value.substring(0, 20) + '...');
            } else {
                $(obj).text(value);
            }
        });
    },
    returnSelectedItem: function() {
        var itemSelected = null;
        $("#bpm-drawing").find(".active").each(function(i, el) {
            itemSelected = el;
        });
        return itemSelected;
    },
    refresh: function() {
        this.isEditionActive = false;
        this.canvas.empty();
        this.aura.hide();
        this.render(this.canvas);
    },
    /************************************************************************
     HANDLER CONFIGURATION SECTION
     ************************************************************************/
    addHandlers: function() {
        // Add movement to items

        if (!this.reviewMode) {
            this.drawing.find("item").draggable({
                containment: "#bpm-drawing",
                grid: [20, 20],
                drag: $.proxy(this.onTaskDragging, this)
            });

            // Configure drop area
            this.configureDroppable();
        }
    },

    addGeneralHandlers: function() {
        // Make items clickable
        this.canvas.on("click", "item", $.proxy(this.showAura, this));

        // Edit items name
        this.canvas.on("dblclick", "item", $.proxy(this.editTaskName, this));

        // Edit task duration
        this.canvas.on("dblclick", "item duration", $.proxy(this.editDuration, this));

        // Make arrows clickable
        this.canvas.on("click", "connector", $.proxy(this.showAuraForArrow, this));

        // Edit arrows name
        this.canvas.on("dblclick", "connector", $.proxy(this.editConnectorName, this));

        // Make canvas click
        this.canvas.on("click", "drawing", $.proxy(this.clickCavnas, this))

        //Set position of label connector
        this.canvas.on("mouseup", "connector label", $.proxy(this.setConnectorPosition, this));

    },
    setConnectorPosition: function(ev) {
        var that = this;
        var label = $(ev.currentTarget);
        var idSourceConnector = $(label).parent().attr("id").split("_to_")[0].split("svgFrom_")[1];
        var idTargetConnector = $(label).parent().attr("id").split("_to_")[1];
        var labelPosition = {
            "x": label.position().left,
            "y": label.position().top
        }
        that.model.changeConnectorPos(idSourceConnector, idTargetConnector, labelPosition);
    },
    clickCavnas: function(ev) {
        var event = ev;
        if (event.target.className === "ui-droppable") {
            this.drawing.find("*").removeClass("active");
            if (this.previusElement) {
                if (this.previusElement.context.nodeName === "CONNECTOR") {
                    var auraShown = this.aura.showForArrow(this.previusElement, false);
                } else {
                    var auraShown = this.aura.showForTask(this.previusElement);
                }
            }
            this.previusElement = null;
        }
    },
    configureDroppable: function() {
        var that = this;
        var mergeElement = false;
        var mergeTarget = null;
        this.drawing.find("item").droppable({
            hoverClass: "dragging-arrow",
            drop: function(event, ui) {
                setTimeout(function() {
                    mergeElement = true;
                    mergeTarget = ui;
                });
            }
        });
        this.drawing.droppable({
            // We use a setTimeout to avoid some weird error from jquery ui
            drop: function(event, ui) {
                setTimeout(function() {
                    if (!mergeElement) {
                        if (ui.helper.hasClass("bpm-cloneable")) that.addTask(ui);
                        else if (ui.helper.hasClass("bpm-moveable")) that.moveTask(ui);
                        else if (ui.helper.hasClass("bpm-option-arrow")) that.addArrow(ui);
                    } else {
                        that.addArrow(mergeTarget);
                        if (mergeElement.successor) {
                            //that.addArrow(mergeTarget);
                        }
                    }
                    mergeElement = false;
                }, 0);
            },
            accept: ".bpm-cloneable, .bpm-moveable, .bpm-option-arrow"
        });
    },

    /************************************************************************
     MODEL MODIFIERS SECTION
     ************************************************************************/
    addTask: function(ui) {
        var type = ui.draggable.data("type");
        var predecessor = ui.draggable.data("predecessor");
        var pos = this.fixPosition(ui.offset);
        var x = pos.left;
        var y = pos.top;
        this.autoAdjustCanvasOn(x, y);
        /* Align to the grid*/
        x = x - (x % 20);
        y = y - (y % 20);
        if (x < 80) x = 80;
        if (y < 60) y = 60;
        this.model.addTask(type, { x: x, y: y }, predecessor);
    },

    /**
     * this method move the canvas if the task or element is near to the right border and bottom border
     * @param {Int} x of element
     * @param {Int} y of element
     */
    autoAdjustCanvasOn: function(x, y) {
        if (this.canvasWidth+this.scrollLeft-200<x) {
            $("move-right").click()
        }
        if (this.canvasHeight+this.scrollTop-100<y) {
            $("move-bottom").click();
        }
    },

    moveTask: function(ui) {
        var type = ui.draggable.data("type");
        var id = ui.draggable.attr("id");
        var x = ui.position.left;
        var y = ui.position.top;

        this.autoAdjustCanvasOn(x, y);
        /* Align to the grid*/
        x = x - (x % 20);
        y = y - (y % 20);
        if (x < 80) x = 80;
        if (y < 60) y = 60;
        this.model.moveTask(id, { x: x, y: y });
    },

    editTaskName: function(ev, params) {
        this.isEditionActive = true;
        if (params) {
            var label = $(ev);
        } else {
            var label = $("label", ev.currentTarget);
        }
        var taskId = label.parent().attr("id");
        var item = this.model.getTaskById(taskId);
        var isEditableName = this.model.isEditableName(item);

        if (isEditableName && !this.reviewMode) {
            var that = this;
            var input = $("<input class='input_name' type='text' />");
            if (label.attr("desc")) {
                input.val(label.attr("desc"));
            } else {
                input.val(label.text());
            }
            if (this.isEditionActive) {
                this.showPopupEditor(label, input, function(value) {
                    that.model.changeTaskName(taskId, value);
                    if (value === "") {
                        value = "";
                    }
                    if (that.setNameTask(value, label)) {
                        that.aux = value;
                        label.attr("desc", value);
                        label.text(value.substring(0, 20) + '...');
                    } else {
                        label.text(value);
                    }
                });
            }
        } else {
            this.isEditionActive = false;
        }
    },
    editConnectorName: function(ev, params ) {
        var that = this;
        if (params) {
            var label = $(ev);
        } else {
            var label = $("label", ev.currentTarget);
        }
        var input = $("<input type='text' maxlength='20' class='input--ConnectorName input_name'/>");
        if (!that.reviewMode) {
            that.isEditionActive = true;
            input.val(label.text());
            var succesorId = label.parent().attr("id");
            var sourceId = label.parent().attr("id");
            succesorId = succesorId.split("_to_")[1]
            sourceId = sourceId.split("_to_")[0].split("svgFrom_")[1]
            this.showPopupEditor(label, input, function(value) {
                that.model.changeConnectorName(sourceId, succesorId, value);
                label.text(value);
            });
        }
    },
    manageExternalOption: function(ev, params) {
        var taskName = this.model.getTaskById(params.task);
        params["taskName"] = taskName.displayName;
        params["type"] = taskName.type;
        this.publish("drawing.externaloption", params);
    },
    clickOnTask: function(ev, params){
        var taskName = this.model.getTaskById(params.task);
        params["taskName"] = taskName.displayName;
        this.publish("drawing.clickOnTask", params);
    },
    editDuration: function(ev, params) {
        ev.stopPropagation()
        if (!this.reviewMode) {
            var that = this;
            that.isEditionActive = true;
            var label = $(ev.currentTarget);
            var taskId = label.parent().attr("id");
            var input = $('<input type="text" maxlength="6" class="input__duration"/>');
            input.bind("paste", function(e) {
                e.preventDefault();
            });
            input.val(label.text());
            this.showPopupEditor(label, input, function(value) {
                if (value === "") {
                    value = "1h";
                }
                var duration = that.model.parseDuration(value);
                if (duration != null) {
                    that.model.changeDuration(taskId, duration);
                    label.text(that.model.formatDuration(duration));
                }
                input.unbind("paste");
            });
        }
    },

    addArrow: function(ui) {
        var source = ui.draggable.data("predecessor");
        var position = this.fixPosition(ui.offset);
        var tasksAtPosition = this.getTasksAtPosition(position);
        if (tasksAtPosition.length == 1) {
            // Add connection in model
            var target = tasksAtPosition.attr("id");
            var classTarget = $(tasksAtPosition).attr("class");
            if (classTarget.indexOf("bpm-StartEvent") != 0 && source !== target) {
                this.model.addConnection(source, target);
            } else {
                this.connector.removeTemporalConnector(this.canvas);
            }
        } else {
            //  Remove temporal arrow
            this.connector.removeTemporalConnector(this.canvas);
        }
    },

    editExpressionForArrow: function(ev, arrowId) {
        var source = bizagi.bpmn.modeler.connector.getSourceTaskFromId(arrowId);
        var target = bizagi.bpmn.modeler.connector.getTargetTaskFromId(arrowId);
        var data = {
            option: "rule",
            source: source,
            target: target
        };
        this.publish("drawing.externaloption", data);
    },

    triggerElseExpressionForArrow: function(ev, arrowId) {
        var source = bizagi.bpmn.modeler.connector.getSourceTaskFromId(arrowId);
        var target = bizagi.bpmn.modeler.connector.getTargetTaskFromId(arrowId);
        var data = {
            option: "else",
            source: source,
            target: target
        };
        this.publish("drawing.externaloption", data);
    },

    setExpressionForArrow: function(sourceTaskId, targetTaskId) {
        this.model.setConnectionRule(sourceTaskId, targetTaskId, "boolean");
    },

    moveCanvas: function(ev, ui) {
        var DISTANCE = 100;
        var element = $(ev.target);
        var tag = element.prop("tagName").toLowerCase();
        if (tag == "move-right") {
            this.scrollLeft += DISTANCE;
            if (this.canvasWidth + this.scrollLeft >= this.drawingWidth) this.drawingWidth += DISTANCE;
        }
        if (tag == "move-left") {
            this.scrollLeft -= DISTANCE;
        }
        if (tag == "move-top") {
            this.scrollTop -= DISTANCE;
        }
        if (tag == "move-bottom") {
            this.scrollTop += DISTANCE;
            if (this.canvasHeight + this.scrollTop >= this.drawingHeight) this.drawingHeight += DISTANCE;
        }

        // Validations
        if (this.scrollTop < 0) this.scrollTop = 0;
        if (this.scrollLeft < 0) this.scrollLeft = 0;

        // Apply changes
        this.drawing.width(this.drawingWidth);
        this.drawing.height(this.drawingHeight);
        this.canvas.scrollLeft(this.scrollLeft);
        this.canvas.scrollTop(this.scrollTop);

        // Update model
        this.model.setCanvasBounds({
            top: this.scrollTop,
            left: this.scrollLeft,
            width: this.drawingWidth,
            height: this.drawingHeight
        });
    },

    /************************************************************************
     DRAGGING HANDLERS
     ************************************************************************/
    onTaskDragging: function(event, ui) {
        this.redrawConnector(ui.helper.attr("id"), ui.position);

        // Move aura
        this.aura.moveTo(ui.helper);
        this.onElementDragging(event, ui);
    },

    onArrowDragging: function(ev, params) {
        var drawing = this.drawing;

        // Fixes position to make it relative to drawing container
        var position = this.fixPosition(params.target);

        // Draw temporal connector
        this.connector.drawTemporalConnector(drawing, drawing.find("#" + params.source), position);

        // Find hovering task
        var draggingOverTasks = this.getTasksAtPosition(position);

        // Remove class to all elements
        drawing.find("item").removeClass("dragging-arrow");

        // Add class to tasks where the cursor is hovering when dragging
        draggingOverTasks.addClass("dragging-arrow");
    },

    onElementDragging: function(ev, ui) {
        // TO BE IMPLEMENTED MAYBE, THE IDEA IS TO PERFORM SCROLL AUTOMATICALLY
    },

    /************************************************************************
     CONNECTORS CONFIGURATION SECTION
     ************************************************************************/
    redrawConnector: function(taskId) {
        var that = this;
        var task = this.model.getTaskById(taskId);
        var drawing = this.drawing;
        $.each(task.predecessors, function(i, predecessorId) {
            if (predecessorId) {
                that.connector.refresh(drawing, drawing.find("#" + predecessorId), drawing.find("#" + taskId));
            }
        });
        $.each(task.successors, function(i, successorId) {
            if (successorId) {
                var task = that.drawing.find("#" + successorId);
                var classD = $(task).attr("class");
                if (classD.indexOf("bpm-StartEvent") != 0) {
                    that.connector.refresh(drawing, drawing.find("#" + taskId), drawing.find("#" + successorId));
                }
            }
        });
    },

    addConnectors: function(viewModel) {
        var that = this;
        var displayName = "";
        var labelConnectorPos = {};
        $.each(viewModel.tasks, function(i, task) {
            $.each(task.successors, function(j, successorId) {
                for (var i = 0; i < task.connectors.length; i++) {
                    if (task.connectors[i].successor == successorId) {
                        displayName = task.connectors[i].displayName;
                        labelConnectorPos = {
                            "x": Math.round(task.connectors[i].displayNamePosition.x),
                            "y": Math.round(task.connectors[i].displayNamePosition.y)
                        }
                    }
                }
                that.addConnector(task.id, successorId, displayName, labelConnectorPos);
            });
        });
    },

    addConnector: function(task1, task2, displayName, labelConnectorPos) {
        var expression = this.model.getConnectionRule(task1, task2);
        var task = this.drawing.find("#" + task2);
        var classD = $(task).attr("class");
        if (classD.indexOf("bpm-StartEvent") != 0) {
            this.connector.connect(this.drawing, this.drawing.find("#" + task1), this.drawing.find("#" + task2), expression, displayName, labelConnectorPos);
        }
    },

    /************************************************************************
     AURA CONFIGURATION SECTION
     ************************************************************************/
    showAura: function(ev) {
        var task = $(ev.currentTarget);
        var auraShown = this.aura.showForTask(task);
        this.model.setAura(task.id, auraShown);
        this.previusElement = task;
        var displayName = null;

        if (task.attr("data-type") === "UserTask") {
            displayName = $(task[0].innerHTML).text().slice(0, -1);
        } else {
            displayName = $(task[0].innerHTML).text();
        }

        this.drawing.find("*").removeClass("active");
        if (auraShown) {
            // Remove active class from all items, and add it to this one
            task.addClass("active");
        }
        var data = {
            task: task[0].id,
            displayName: displayName,
            type: task.attr("data-type"),
            option: 'selecTask'
        };
        this.publish("drawing.selectedTask", data);
        // ev.stopImmediatePropagation();

        if (this.reviewMode) {
            $(task).parent().find('aura .wrapper-aura-options, aura .wrapper-options .bpm-option-change, aura .wrapper-options .bpm-option-delete').hide();
        }

    },
    showAuraForArrow: function(ev) {
        var arrow = $(ev.currentTarget);
        this.previusElement = arrow;
        var id = arrow.attr("id");
        var sourceTask = bizagi.bpmn.modeler.connector.getSourceTaskFromId(id);
        var showRuleOption = this.model.successorConnectionsCanContainRule(sourceTask);
        var auraShown = this.aura.showForArrow(arrow, showRuleOption);
        this.drawing.find("*").removeClass("active");
        if (auraShown) {
            arrow.addClass("active");
        }
        if (this.reviewMode) {
            $(arrow).parent().find('.bpm-aura-for-arrow .wrapper-options').css('width', '27px');
            $(arrow).parent().find('.bpm-aura-for-arrow .wrapper-options special-options .bpm-option-else, .bpm-aura-for-arrow .wrapper-options special-options .bpm-option-delete').hide();
        }
        if (this.reviewMode && $(arrow).hasClass('bpm-else')) {
            $(arrow).parent().find('.bpm-aura-for-arrow .wrapper-options').hide();
        }
    },
    /************************************************************************
     HELPERS SECTION
     ************************************************************************/
    getTasksAtPosition: function(position) {
        return this.drawing.find("item")
            .filter(function() {
                var el = $(this);
                var pos = el.position();
                var coords = {
                    x: pos.left - 25,
                    y: pos.top - 25,
                    w: el.width() + 25,
                    h: el.height() + 25
                };
                return (coords.x <= position.left && position.left <= (coords.x + coords.w)) &&
                    (coords.y <= position.top && position.top <= (coords.y + coords.h));
            });
    },

    showPopupEditor: function(label, input, callback) {
        var that = this;
        var cancelAction = false;
        // Create input
        label.hide();
        label.after(input);
        input.focus();
        input.select();

        $("#bpm-drawing").find(".ui-droppable").bind("keydown", function(e) {
            switch (e.which) {
                case 27:
                    cancelAction = true;
                    closeEditor();
                    break;
            }
        });

        $(label).parent().find(".input_name").each(function(index, el) {
            if (index === 1) {
                $(el).remove();
            }
        });
        $(label).parent().find(".input_name").focus();


        var closeEditor = function() {
            if (cancelAction) {
                callback(label.text());
                label.show();
                input.detach();
                cancelAction = false;
            } else {
                var value = input.val();
                callback(value);
                label.show();
                $(label).parent().attr("title", $("label.labelNameTask.label-UserTask").text());
                input.detach();
            }
            that.model.triggerModelChange(true);
            that.isEditionActive = false;
        };

        // Change handler
        input.keypress(function(ui) {
            if (that.isEditionActive && ui.key == "Enter") {
                closeEditor();
            }

        });

        // Close up handler
        input.click(function(e) { e.preventDefault(); return false; });

        $("#canvas").one("click", function() {
            if (that.isEditionActive) {
                closeEditor();
            }
        });
    },

    fixPosition: function(position) {
        // Fixes position to make it relative to drawing container
        var relative = this.drawing.offset();
        position.left -= relative.left;
        position.top -= relative.top;
        return position;
    },
    setNameTask: function(text, obj) {
        if (text.length > 20 || obj.height() > 34) {
            return true;
        }
    }

});