bizagi.bpmn.modeler.observable.extend("bizagi.bpmn.modeler.aura", {
    /* STATIC STUFF */
    externalOptions: {
        "form": "bz-icon bz-icon-template-outline",
        "assignee": "bz-icon bz-icon-cog-outline",
        "notify": "bz-icon bz-icon-cog-outline"
    },
    getClassForExternalOption: function(type) {
        return this.externalOptions[type];
    },
    getTitleForType: function(type) {
        return this.toolBarTitles[type];
    }
}, {

    /* INSTANCE STUFF */
    /* Constructor */
    init: function(model) {
        // Define converter for task type class        
        $.views.converters("externaloption_class", function(val) {
            return bizagi.bpmn.modeler.aura.getClassForExternalOption(val);
        });

        $.views.converters("getResourceLocalization", function(val) {
            var key = '';
            var result = '';
            if(bizagi.bpmn.modeler.viewer.featureLocalization){
                key = bizagi.bpmn.modeler.toolbar.getTitleForType(val);
                result = bizagi.bpmn.modeler.viewer.getKeyLocalization(key);
            }else{
                result = bizagi.bpmn.modeler.toolbar.getOldLocalization(val);
            }
            return result;
        });
        $.templates({
            bpm_modeler_aura: '\
                <aura class="bpm-aura-for-{{:type}}"> \
                 \ <div class="wrapper-options">\
                        {{if changeTo && changeTo.length > 0}} \
                            <special-option class="tooltip bpm-option-change bz-icon bz-icon-asynchronous-outline"><span class="tooltiptext tooltip-right"><p>{{title: "changeType"}}</p></span></special-option> \
                                <aura-change> \
                                    {{for changeTo tmpl="bpm_modeler_auraChangeTo"/}} \
                                </aura-change> \
                        {{/if}} \
                    {{if externalOptions && externalOptions.length > 0}} \
                        <external-options> \
                            {{for externalOptions tmpl="bpm_modeler_auraExternalOption"/}} \
                        </external-options> \
                    {{/if}} \
                     \<special-options {{if showRuleOption}} class="bpm-show-rule-option" {{/if}}> \
                        {{if showRuleOption}} \
                            <special-option class="tooltip bpm-option-expression bpmn-icon-conditional-flow"><span class="tooltiptext tooltip-right"><p>{{title: "addExpression"}}</p></span></special-option> \
                            <special-option class="tooltip bpm-option-else bpmn-icon-default-flow"><span class="tooltiptext tooltip-right"><p>{{title:"elseExpression"}}</p></span></special-option> \
                        {{/if}} \
                        <special-option class="tooltip bpm-option-delete bpmn-icon-trash bpmn-icon-trash-{{:type}}"><span class="tooltiptext tooltip-right"><p>{{title: "deleteShape"}}</p></span></special-option> \
                        </special-options> \
                         \ <aura-confirmation-delete class="popup__confirmationDelete"> \
                                                    <div>\
                                                        <p>{{getResourceLocalization: "deleteConfirmationMessage"}}</p>\
                                                        <div class="contentButtons">\
                                                            <div class="btn btn__confirmation btn__confirmation--yes">{{getResourceLocalization: "confirmDeleteShape"}}</div>\
                                                            <div class="btn btn__confirmation btn__confirmation--no">{{getResourceLocalization: "cancelDeleteShape"}}</div>\
                                                        </div>\
                                                        <span class="downArrow"></span>\
                                                    </div>\
                                                </aura-cofirmation-delete>\
                        </div>\
                        \<div class="wrapper-aura-options">\
                            {{for options tmpl="bpm_modeler_auraOption"/}} \
                             \{{if showArrow}} \
                            <aura-option class="tooltip bpm-option-arrow bpmn-icon-connection" data-predecessor="{{:~root.id}}"><span class="tooltiptext tooltip-right"><p>{{title: "flowSecuence"}}</p></span></aura-option> \
                        {{/if}} \
                        \</div>\
                  </aura>',
            bpm_modeler_auraOption: '<aura-option class="{{if ~#data=="IntermediateEvent"}}bpm-aura-intermediate-icon{{/if}} tooltip bpm-{{:#data}} {{tasktype_class: #data}} bpm-cloneable" \
                                            data-type="{{:#data}}" \
                                            data-predecessor="{{:~root.id}}">\
                                            <span class="tooltiptext tooltip-right"><p>{{title: #data}}</p></span>\
                                        </aura-option>',
            bpm_modeler_auraChangeTo: '<aura-change-to class="tooltip bpm-{{:#data}} {{tasktype_class: #data}}"  \
                                                data-type="{{:#data}}" \
                                                data-task="{{:~root.id}}">\
                                                <span class="tooltiptext tooltip-right"><p>{{title: #data}}</p></span>\
                                        </aura-change-to>',
            bpm_modeler_auraExternalOption: '{{if (~root.type ==="EmailTask" && #data ==="notify") || ((~root.type === "UserTask" || ~root.type=="IntermediateEvent") && (#data === "form" || #data ==="assignee") )}} \
                                            <external-option class="tooltip bpm-{{:#data}} {{externaloption_class: #data}}" \
                                                        data-option="{{:#data}}" \
                                                        data-task="{{:~root.id}}">\
                                                        <span class="tooltiptext tooltip-right"><p>{{title: #data}}</p></span>\
                                                </external-option>{{/if}}',
        });

        // Set class variables
        this.model = model;
        this.previousElementId = null;
        this.aura = null;
        this.isActiveConfirmDialog = false;
    },
    /*FUNCTION FOR VALIDATE IF THE ELEMNT ADD ARROW OPTION*/
    isEndEvent: function(task) {
        if (task.data("type") === 'EndEvent' || task.data("type") === 'TerminateEvent') {
            return false;
        } else {
            return true;
        }
    },
    /************************************************************************
     SHOW METHODS SECTION
     ************************************************************************/
    showForTask: function(task) {
        var that = this;
        var taskId = task.attr("id");
        var typeShape = this.model.getTaskById(taskId);
        var params = {
            type: typeShape.type,
            options: this.getOptions(task),
            changeTo: this.getChangeTo(task),
            externalOptions: this.getExternalOptions(task),
            showArrow: this.isEndEvent(task),
            positioning: "right"
        };

        // Show aura
        var shown = this.show(task, params);

        if (shown) {
            // Put special options at bottom
            var specialOptions = this.aura.find(".wrapper-options");
            var typeItem = this.model.getTaskById(task.attr("id"));
            specialOptions.css('display', 'absolute');

            switch (typeItem.type) {
                case 'UserTask':
                    specialOptions.position({ of: task, my: "center-47 top-35", at: "center top", collision: "none" });
                    // Publish event
                    this.publish("task.click", { task: taskId, option: "assignee", type: typeItem.type }); //To detect click in the task and inform angular code to keep the properties right side bar if is is open 
                    break;
                case 'EmailTask':
                    specialOptions.position({ of: task, my: "center-47 top-35", at: "center top", collision: "none" });
                    break;
                case 'IntermediateEvent':
                    specialOptions.position({ of: task, my: "center-43 top-42", at: "center top", collision: "none" });
                    this.publish("task.click", { task: taskId, option: "assignee", type: typeItem.type }); //To detect click in the task and inform angular code to keep the properties right side bar if is is open
                    break;
                case 'StartEvent':
                    specialOptions.position({ of: task, my: "center top-48", at: "center top", collision: "none" });
                    break;
                case 'EndEvent':
                case 'TerminateEvent':
                case 'ParallelGateway':
                case 'ExclusiveGateway':
                    specialOptions.position({ of: task, my: "center-18 top-48", at: "center top", collision: "none" });
                    break;
                default:
                    specialOptions.position({ of: task, my: "center-8 top+25", at: "center bottom", collision: "none" });
                    break;
            }



            // Manage changeTo handler
            this.aura.find("special-option.bpm-option-change").on("click", function() { that.showChangeToOptions(this, typeItem.type); });

            // Manage delete handler
            this.aura.find("special-option.bpm-option-delete").on("click", function() { that.activePopupConfirmationDelete(this, taskId); });

            //new
            this.aura.find("aura-confirmation-delete .contentButtons .btn").on("click", function() { that.actionConfirmDelete(this, taskId); });

            // Manage external options handler
            this.aura.find("external-option").on("click", function() {
                that.manageExternalOption($(this), taskId);
            });

            // Make arrow dragabble
            this.aura.find("aura-option.bpm-option-arrow").draggable({
                helper: "clone",
                containment: "#bpm-drawing",
                grid: [20, 20],
                drag: function(ev, ui) { that.dragArrow(ui, taskId); }
            });
        }

        return shown;
    },

    showForArrow: function(arrow, showRuleOption) {
        var that = this;
        var arrowId = arrow.attr("id");
        var params = {
            type: "arrow",
            positioning: "center",
            showRuleOption: showRuleOption
        }

        // Show aura
        var shown = this.show(arrow, params);

        if (shown) {
            // Put special options at bottom
            var specialOptions = this.aura.find(".wrapper-options");
            specialOptions.position({ of: arrow, my: "center center", at: "center+20 center-20", collision: "none" });

            this.aura.find("special-option.bpm-option-delete").addClass("bpmn-icon-trash--arrow");
            // Manage delete handler
            this.aura.find("special-option.bpm-option-delete").on("click", function() { that.deleteArrow(arrowId); });

            // Manage rule handler
            this.addHandlersForExpressions(arrowId);
        }

        return shown;
    },

    show: function(element, params) {
        var that = this;
        var elementId = element.attr("id");

        // Check previous element to toggle on/off
        if (this.previousElementId) {
            var isSame = this.previousElementId == elementId;
            this.hide(this.previousElementId);
            if (isSame) return false;
        }
        this.previousElementId = elementId;

        // Render template
        var aura = $.templates.bpm_modeler_aura.render($.extend(params, { id: elementId }));
        this.aura = $(aura);
        element.after(this.aura);
        this.moveTo(element, params.positioning);

        // Put external options at top
        if (params.externalOptions) {
            var externalOptions = this.aura.find("external-options");
            //externalOptions.position({ of: element, my: "center-42 bottom+16", at: "center top", collision: "none" });
        }

        // Make items draggable
        this.aura.find("aura-option").draggable({
            helper: "clone",
            containment: "#bpm-drawing",
            grid: [20, 20],
            drag: function(ev, ui) { that.publish("item.drag", ui); }
        });

        return true;
    },

    /************************************************************************
     OTHER PUBLIC METHODS SECTION
     ************************************************************************/

    hide: function(elementId) {
        elementId = elementId || this.previousElementId;
        if (elementId == null) return;
        if (this.aura) this.aura.detach();
        this.previousElementId = null;
    },

    moveTo: function(element, positioning) {
        var id = element.attr("id");
        if (id != this.previousElementId) {
            this.hide();
            return;
        }
        // Only perform the move when the aura is displayed for the given task
        positioning = positioning || "right";
        if (positioning == "right")
            this.aura.position({ of: element, my: "left+20 center", at: "right center", collision: "none" });
    },


    /************************************************************************
     HANDLER METHODS SECTION
     ************************************************************************/
    showChangeToOptions: function(element, type) {
        var changeToOptions = this.aura.find("aura-change");
        changeToOptions.toggleClass("visible");
        changeToOptions.position({ of: element, my: "center top-60", at: "center top", collision: "none" });
        //Change position of aura-change when the element is on top limit.
        var auraTopPosition = this.aura.position().top;
        var limitTopPosition = type === "UserTask" ? 57 : 120;
        if (auraTopPosition < limitTopPosition) {
            changeToOptions.css({'top':33});
        }
        // Add click handlers
        changeToOptions.find("aura-change-to").click($.proxy(this.changeTo, this));
    },

    addHandlersForExpressions: function(arrowId) {
        var that = this;

        // Add click handlers
        this.aura.find(".bpm-option-else").click(function() {
            var source = bizagi.bpmn.modeler.connector.getSourceTaskFromId(arrowId);
            var target = bizagi.bpmn.modeler.connector.getTargetTaskFromId(arrowId);

            // Publish event
            that.publish("arrow.setElse", arrowId);

            // Change model
            that.model.setConnectionRule(source, target, "else");
        });
        this.aura.find(".bpm-option-expression").click(function() {
            // Publish event
            that.publish("arrow.setExpression", arrowId);
        });
    },
    activePopupConfirmationDelete: function(element, taskId) {
        var item = this.model.getTaskById(taskId);
        var deleteOptions = this.aura.find(".popup__confirmationDelete");
        var left = $(element).position().left;
        var top = $(element).position().top;

        if (this.model.activeConfirmationPopup(item.type)) {
            this.isActiveConfirmDialog = true;
            if (item.position.y < 170) {
                $(deleteOptions).css({ "top": top + 38, "left": left - 90 });
                $(deleteOptions).find(".downArrow").addClass("rotateArrow");
            } else {

                $(deleteOptions).css({ "top": top - 115, "left": left - 90 })
            }
            $(deleteOptions).show();
        } else {
            this.deleteTask(taskId);
        }

    },
    actionConfirmDelete: function(ev, taskId) {
        var event = ev;
        var btn = event.className;
        var deleteOptions = this.aura.find(".popup__confirmationDelete");
        if (btn.indexOf('--yes') > 0) {
            this.deleteTask(taskId);
        } else {
            this.isActiveConfirmDialog = false;
            $(deleteOptions).hide();
        }
    },
    deleteTask: function(taskId) {
        this.model.deleteTask(taskId);
    },

    deleteArrow: function(arrowId) {
        var source = bizagi.bpmn.modeler.connector.getSourceTaskFromId(arrowId);
        var target = bizagi.bpmn.modeler.connector.getTargetTaskFromId(arrowId);
        this.model.deleteArrow(source, target);
    },

    dragArrow: function(ui, elementId) {
        // Publish event
        this.publish("arrow.drag", { source: elementId, target: ui.offset });
    },

    changeTo: function(ev) {
        var changeOption = $(ev.currentTarget);
        var type = changeOption.data("type");
        var task = changeOption.data("task");

        // Publish event
        this.model.changeTaskType(task, type);
    },

    manageExternalOption: function(externalOption, taskId) {
        var option = externalOption.data("option");
        // Publish event
        this.publish("task.externaloption", { task: taskId, option: option });
    },

    /************************************************************************
     MISC METHODS SECTION
     ************************************************************************/
    getOptions: function(task) {
        var type = task.data("type");
        if (type == "EndEvent" || type == 'TerminateEvent') return [];

        return [
            "UserTask",
            "IntermediateEvent",
            "ExclusiveGateway",
            "EndEvent"
        ];
    },

    getChangeTo: function(task) {
        var type = task.data("type");
        var possibleChanges = [];
        if (type == "ExclusiveGateway" || type == "ParallelGateway" || type == "InclusiveGateway" || type == "ComplexGateway") {
            //possibleChanges =  [ "ParallelGateway", "ExclusiveGateway"];
            possibleChanges = ["ParallelGateway", "InclusiveGateway", "ComplexGateway", "ExclusiveGateway"];
        } else if (type == "EndEvent" || type == "TerminateEvent") {
            possibleChanges = ["EndEvent", "TerminateEvent"];
        } else if (type === "UserTask" || type === "EmailTask") {
            possibleChanges = ["EmailTask", "UserTask"];
        }
        if (possibleChanges.length > 0) {
            possibleChanges.removeOne(type);
            return possibleChanges
        }
        return null;
    },

    getExternalOptions: function(task) {
        var type = task.data("type");
        if (type != "UserTask" && type != "IntermediateEvent" && type != "EmailTask") return [];

        return ["form", "assignee", "notify"];
    }
});