bizagi.bpmn.modeler.observable.extend("bizagi.bpmn.modeler.toolbar", {

    /* STATIC STUFF */
    bpmShapes: {
        "StartEvent": "bpmn-icon-start-event-none",
        "IntermediateEvent": "bpmn-icon-intermediate-event-none",
        "EndEvent": "bpmn-icon-end-event-none",
        "TerminateEvent": "bpmn-icon-end-event-terminate",
        "ExclusiveGateway": "bpmn-icon-gateway-xor",
        "ParallelGateway": "bpmn-icon-gateway-parallel",
        "InclusiveGateway": "bpmn-icon-gateway-or",
        "ComplexGateway": "bpmn-icon-gateway-complex",
        "UserTask": "bpmn-icon-task",
        "EmailTask": "ico_mail bpmn-icon-intermediate-event-throw-escalation"
    },
    toolBarTitlesOld: {
        "StartEvent": "Start Event",
        "IntermediateEvent": "Intermediate Event",
        "EndEvent": "End Event",
        "ExclusiveGateway": "Exclusive Gateway",
        "UserTask": "Task",
        "TerminateEvent": "Terminate Event",
        "ParallelGateway": "Parallel Gateway",
        "assignee": "Settings",
        "form": "Form Designer",
        "EmailTask": "Email Task",
        "notify": "Email Settings",
        "changeType": "Change type",
        "deleteShape": "Delete",
        "flowSecuence": "Sequence Flow",
        "deleteConfirmationMessage": "Do you want to DELETE this element? By doing so you will delete all the configurations associated to it.",
        "confirmDeleteShape": "Yes",
        "cancelDeleteShape": "No",
        "addExpression": "Define expressions",
        "elseExpression": "Else"
    },
    toolBarTitles: {
        "StartEvent": "workportal-process-modeler-startEvent",
        "IntermediateEvent": "workportal-process-modeler-intermediateEvent",
        "EndEvent": "workportal-process-modeler-endEvent",
        "ExclusiveGateway": "workportal-process-modeler-exclusiveGateway",
        "UserTask": "workportal-process-modeler-userTask",
        "TerminateEvent": "workportal-process-modeler-terminateEvent",
        "ParallelGateway": "workportal-process-modeler-parallelGateway",
        "assignee": "workportal-process-modeler-settings",
        "form": "workportal-process-modeler-formDesigner",
        "EmailTask": "workportal-process-modeler-emailTask",
        "notify": "workportal-process-modeler-emailSettings",
        "changeType": "workportal-process-modeler-changeType",
        "deleteShape": "workportal-process-modeler-deleteShape",
        "flowSecuence": "workportal-process-modeler-flowSecuence",
        "deleteConfirmationMessage": "workportal-process-modeler-deleteConfirmationMessage",
        "confirmDeleteShape": "workportal-process-modeler-confirm-delete",
        "cancelDeleteShape": "workportal-process-modeler-cancel-delete",
        "addExpression": "workportal-process-modeler-rules-addExpression",
        "elseExpression": "workportal-process-modeler-rules-elseExpression"
    },
    getClassForType: function(type) {
        return this.bpmShapes[type];
    },
    getTitleForType: function(type) {
        return this.toolBarTitles[type];
    },
    getOldLocalization: function(type){
        return this.toolBarTitlesOld[type];
    },

}, {

    /* INSTANCE STUFF */

    /* Constructor */
    init: function() {
        // Define converter for task type class
        var that = this;
        $.views.converters("tasktype_class", function(val) {
            return bizagi.bpmn.modeler.toolbar.getClassForType(val);
        });

        $.views.converters("title", function(val) {
            var result = '';
            var key = '';
            if(bizagi.bpmn.modeler.viewer.featureLocalization){
                key = bizagi.bpmn.modeler.toolbar.getTitleForType(val);
                result = bizagi.bpmn.modeler.viewer.getKeyLocalization(key);
            }else{
                result = bizagi.bpmn.modeler.toolbar.getOldLocalization(val);
            }
            return result;
        });

        $.templates({
            bpm_modeler_toolbar: '<toolbar> {{for items tmpl="bpm_modeler_toolbarItem"/}}</toolbar>',
            bpm_modeler_toolbarItem: '<item class="tooltip bpm-{{:#data}} {{tasktype_class: #data}}  bpm-cloneable" data-type="{{:#data}}">{{if #data=="IntermediateEvent"}}<svg id="Layer_1" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><defs><style>.cls-11{fill:#fff;}.cls-22{fill:#9ca613;}</style></defs><title>event 2</title><circle class="cls-11" cx="12" cy="12" r="12"/><path class="cls-22" d="M12,1.12A10.88,10.88,0,1,1,1.12,12,10.89,10.89,0,0,1,12,1.12M12,0A12,12,0,1,0,24,12,12,12,0,0,0,12,0Z"/><path class="cls-22" d="M12,3.79A8.21,8.21,0,1,1,3.79,12,8.22,8.22,0,0,1,12,3.79m0-1.12A9.33,9.33,0,1,0,21.33,12,9.33,9.33,0,0,0,12,2.67Z"/></svg>{{/if}}<span class="tooltiptext tooltip-right"><p>{{title: #data}}</p></span></item>'
        });
        // Define items
        this.items = [
            "StartEvent",
            "UserTask",
            "ExclusiveGateway",
            "IntermediateEvent",
            "EndEvent"
        ];
    },

    render: function(canvas) {
        var that = this;
        this.canvas = canvas;

        // Render template
        var html = $.templates.bpm_modeler_toolbar.render({ items: this.items });
        this.canvas.append(html);

        // Make items draggable
        canvas.find("item").draggable({
            helper: "clone",
            containment: "#bpm-drawing",
            grid: [20, 20],
            drag: $.proxy(this.onItemDragging, this)
        });
    },

    onItemDragging: function(event, ui) {
        this.publish("item.dragging", ui);
    }
});