bizagi.bpmn.modeler.observable.extend("bizagi.bpmn.modeler.viewer", {
    // Static Values
    featureLocalization: false,
    // Static Functions
    getInstaceLocalizationService: function() {
        if (window.parent && window.parent.bizagi.localization) {
            return window.parent.bizagi.localization;
        } else {
            return window.bizagi.localization;
        }
    },
    getKeyLocalization: function(key) {
        var localizator = this.getInstaceLocalizationService();
        var result = localizator.getResource(key);
        return result;
    }
}, {

    /* Constructor */
    init: function(params) {
        $.templates({
            bpm_modeler_viewer_container: '<bpm-modeler> \
                            <div id="bpm-toolbar"></div> \
                            <div id="bpm-drawing"></div> \
                            <move-helper> \
                                <move-top class="bpm-move"></move-top> \
                                <move-left class="bpm-move"></move-left> \
                                <move-bottom class="bpm-move"></move-bottom> \
                                <move-right class="bpm-move"></move-right> \
                            </move-helper> \
                        </bpm-modeler>'
        });

        // Create  model
        this.model = new bizagi.bpmn.modeler.model();
        this.model.subscribe("model.change", $.proxy(this.changed, this));
        this.model.subscribe("shape.change", $.proxy(this.triggerChangeShape, this));
        if (params && params.model) this.model.setModel(params.model);

        // Declare dependencies
        this.drawing = new bizagi.bpmn.modeler.drawing(this.model);
        this.drawing.subscribe("drawing.externaloption", $.proxy(this.manageExternalOption, this));
        this.drawing.subscribe("drawing.selectedTask", $.proxy(this.selectedTask, this));
        this.drawing.subscribe("drawing.canvasEvent", $.proxy(this.canvasEvent, this));
        this.drawing.subscribe("load.finish", $.proxy(this.triggerLoadFinish, this));
        this.drawing.subscribe("drawing.clickOnTask", $.proxy(this.clickOnTask, this));
        this.toolbar = new bizagi.bpmn.modeler.toolbar();
        this.toolbar.subscribe("item.dragging", $.proxy(this.onToolbarItemDragging, this));
        this.readOnly = false;
        if (params === false || !params) {
            this.readOnly = false;
        } else {
            this.readOnly = true;
        }
    },

    render: function(canvas, diagram) {
        var self = this;
        
        self.canvas = canvas;

        // Render container
        var container = $.templates.bpm_modeler_viewer_container.render();
        self.canvas.html(container);

        $.when(self.checkCSSLoaded()).then(function(){
            // Render dependencies
            self.renderToolbar();
            self.renderDrawing(self.readOnly);
    
            if (diagram) self.load(diagram);
            if (self.readOnly) {
                $("#bpm-toolbar").css({ "display": "none" });
                $("#bpm-drawing").addClass("bpm-modeler-readOnly");
                $("#bpm-drawing").bind("over");
            }
            // Assign move handlers
            self.canvas.on("click", ".bpm-move", $.proxy(self.moveCanvas, self))
        });
    },

    checkCSSLoaded: function(){
        var self = this;
        var defer = $.Deferred();
        if(self.cssAppliedOnDrawing()){
            defer.resolve();
        }
        else{
            var MAX_COUNTER_CHECK_CSS = 10;
            var counterCheckCSS = 0;
            var validateCSSLoaded = setInterval(function(){
                counterCheckCSS++;
                if(self.cssAppliedOnDrawing() > 0 || MAX_COUNTER_CHECK_CSS <= counterCheckCSS){
                    defer.resolve();
                    clearInterval(validateCSSLoaded);
                }
            }, 1000);
        }
        
        return defer.promise();
    },

    cssAppliedOnDrawing: function(){
        return $("bpm-modeler #bpm-drawing").height() > 0;
    },

    reviewMode: function(activeModeReview) {
        if (activeModeReview) this.drawing.activeReviewMode();
    },

    localizationModeler: function( activelocalizationModeler ){
        bizagi.bpmn.modeler.viewer.featureLocalization = activelocalizationModeler;
    },
    renderToolbar: function() {
        var toolbarCanvas = this.canvas.find("#bpm-toolbar");
        this.toolbar.render(toolbarCanvas);
    },

    renderDrawing: function(state) {
        var drawingCanvas = this.canvas.find("#bpm-drawing");
        this.drawing.render(drawingCanvas, state);
    },

    moveCanvas: function(ev, ui) {
        this.drawing.moveCanvas(ev, ui);
    },

    onToolbarItemDragging: function(event, ui) {
        this.drawing.onElementDragging(event, ui);
    },

    manageExternalOption: function(ev, params) {
        this.publish("bpm.modeler.externalOption", params);
    },
    selectedTask: function(ev, params) {
        this.publish("bpm.modeler.selectedTask", params);
    },
    canvasEvent: function(ev, params) {
        this.publish("bpm.modeler.canvasEvent", params);
    },
    triggerLoadFinish: function() {
        this.publish("bpm.modeler.loadFinish");
    },
    clickOnTask: function(ev, params){
        this.publish("bpm.modeler.clickOnTask", params);
    },
    setConnectionExpression: function(source, target) {
        this.drawing.setExpressionForArrow(source, target);
    },

    save: function() {
        return this.model.getPersistenceModel();
    },

    load: function(json) {
        this.model.setModel(json);
    },

    changed: function(params) {
        this.publish("bpm.modeler.changed");
    },
    triggerChangeShape: function() {
        this.publish("bpm.modeler.changeShape");
    },
    setExtendedProperty: function(taskId, property, value) {
        this.model.setExtendedProperty(taskId, property, value);
    },

    getExtendedProperty: function(taskId, property) {
        return this.model.getExtendedProperty(taskId, property);
    }
});

// Utility functions -> Move to bizagi.js
if (!Array.prototype.removeOne) {
    Array.prototype.removeOne = function(item) {
        for (var i = this.length; i--;) {
            if (this[i] === item) {
                this.splice(i, 1);
                return;
            }
        }
    };
}
if (!Array.prototype.removeAll) {
    Array.prototype.removeAll = function(item) {
        for (var i = this.length; i--;) {
            if (this[i] === item) {
                this.splice(i, 1);
            }
        }
    };
}