bizagi.bpmn.modeler.observable.extend("bizagi.bpmn.modeler.model", {}, {

    /* Constructor */
    init: function() {
        this.tasks = {};
        this.canvas = {};
        var that = this;
    },

    setModel: function(model) {
        this.tasks = model.tasks;
        this.canvas = model.canvas;
        this.triggerModelRedraw();
    },

    /************************************************************************
     GETTERS SECTION
     ************************************************************************/

    getViewModel: function() {
        var task_list = [];
        for (val in this.tasks) {
            if (this.tasks[val].durationInMinutes != null && this.tasks[val].type === "UserTask") {
                var formatDuration = this.formatDuration(this.tasks[val].durationInMinutes);
                this.tasks[val].durationInMinutes = formatDuration;
            }
            task_list.push(this.tasks[val]);
        }
        return {
            tasks: task_list,
            canvas: this.canvas,
            isGateway: this.isGateway,
            isEvent: this.isEvent,
            isEmailTask: this.isEmailTask
        };
    },
    getPersistenceModel: function() {
        var that = this;
        var result = { tasks: jQuery.extend({}, this.tasks), canvas: jQuery.extend({}, this.canvas) };
        $.each(result.tasks, function(i, task) {
            if (task.durationInMinutes) {
                task.durationInMinutes = that.parseDuration(task.durationInMinutes);
            }
            // Remove showAura
            delete task.showAura;
        });
        return result;
    },
    getTaskById: function(id) {
        return this.tasks[id];
    },

    getConnectionRule: function(sourceTaskId, targetTaskId) {
        var source = this.tasks[sourceTaskId];
        if (!this.isGateway(source.type)) return null;

        // Connection rules only work for gateways
        var rule = $.grep(source.rules, function(rule) {
            return rule.target == targetTaskId;
        });
        //return rule.length > 0? rule[0].expression: "missing";
        return rule.length > 0 ? rule[0].expression : null;
    },

    successorConnectionsCanContainRule: function(task) {
        var task = this.tasks[task];
        if (task.type === 'ParallelGateway') {
            return false;
        } else {
            return this.isGateway(task.type);
        }
    },

    /************************************************************************
     CANVAS MANAGEMENT SECTION
     ************************************************************************/
    setCanvasBounds: function(bounds) {
        this.canvas = bounds;
        this.triggerModelChange({ redraw: false });
    },

    /************************************************************************
     EXTENDED PROPERTIES SECTION
     ************************************************************************/

    setExtendedProperty: function(id, property, value) {
        if (this.tasks[id]) {
            // make sure we have extendedProperties
            this.tasks[id].extendedProperties = this.tasks[id].extendedProperties || {};
            this.tasks[id].extendedProperties[property] = value;

            // Publish event but don't redraw'
            this.triggerModelChange({ redraw: false });
        }
    },

    getExtendedProperty: function(id, property) {
        if (this.tasks[id]) {
            return this.tasks[id].extendedProperties[property];
        }
        return null;
    },

    /************************************************************************
     ADD SECTION
     ************************************************************************/

    addTask: function(taskType, position, predecessor) {
        var task = {
            id: this.newGuid(),
            type: taskType,
            displayName: this.getDefaultName(taskType),
            durationInMinutes: this.getDuration(taskType),
            position: position,
            showAura: false,
            predecessors: [],
            successors: [],
            connectors: [],
            rules: [],
            extendedProperties: {}
        };
        // Add predecessor
        if (predecessor) task.predecessors.push(predecessor);

        // Add the new task
        this.tasks[task.id] = task;

        // Add succesor to predecessor
        if (this.tasks[predecessor]) {
            this.tasks[predecessor].successors.push(task.id);
            this.tasks[predecessor].connectors.push({
                "successor": task.id,
                "displayName": "",
                "displayNamePosition": {
                    "x": 20,
                    "y": 20
                }
            });
        }
        // Set the new task, to use the aura
        this.setAura(task.id, true);

        // Publish event
        this.triggerModelChange();
    },

    addConnection: function(sourceTaskId, targetTaskId) {
        var source = this.tasks[sourceTaskId];
        var target = this.tasks[targetTaskId];
        var isPresent = false;
        for (var key in source.successors) {
            if (source.successors[key] === targetTaskId) isPresent = true;
        }
        if (!isPresent) {
            source.successors.push(targetTaskId);
            source.connectors.push({
                "successor": targetTaskId,
                "displayName": "",
                "displayNamePosition": {
                    "x": 20,
                    "y": 20
                }
            });
            target.predecessors.push(sourceTaskId);
        }
        // Publish event
        this.triggerModelChange();
    },

    /************************************************************************
     EDIT SECTION
     ************************************************************************/

    changeTaskName: function(id, newDisplayName) {
        if (this.tasks[id]) {
            if (this.tasks[id].displayName == newDisplayName) return;
            this.tasks[id].displayName = newDisplayName;
            // Publish event but don't redraw'
            this.triggerModelChange({ redraw: false });
        }
    },
    changeConnectorName: function(source, successor, connectorName) {
        if (this.tasks[source]) {
            for (var i = 0; i < this.tasks[source].connectors.length; i++) {
                if (this.tasks[source].connectors[i].successor == successor) {
                    this.tasks[source].connectors[i].displayName = connectorName;
                }
            }
            this.triggerModelChange({ redraw: false });
        }
    },
    changeConnectorPos: function(idSourceTask, idTargetTask, labelPosition) {
        if (this.tasks[idSourceTask]) {
            for (var i = 0; i < this.tasks[idSourceTask].connectors.length; i++) {
                if (this.tasks[idSourceTask].connectors[i].successor == idTargetTask) {
                    this.tasks[idSourceTask].connectors[i].displayNamePosition.x = Math.round(labelPosition.x);
                    this.tasks[idSourceTask].connectors[i].displayNamePosition.y = Math.round(labelPosition.y);
                }
            }
            this.triggerModelChange({ redraw: false });
        }
    },
    changeDuration: function(id, duration) {
        if (this.tasks[id]) {
            if (this.tasks[id].durationInMinutes == duration) return;
            this.tasks[id].durationInMinutes = duration;
            // Publish event but don't redraw'
            this.triggerModelChange({ redraw: false });
        }
    },

    changeTaskType: function(id, newType) {
        if (this.tasks[id]) {
            if (this.tasks[id].type == newType) return;
            this.tasks[id].type = newType;
            // if (newType === "EmailTask") this.tasks[id].duration = this.getDuration(this.tasks[id].type);
            //if (newType === "UserTask") this.tasks[id].duration = this.tasks[id].duration;
            // Publish event
            this.triggerModelChange();
            this.triggerChangeShape();
        }
    },

    moveTask: function(id, position) {
        if (this.tasks[id]) {
            this.tasks[id].position = position;

            // Publish event
            this.triggerModelChange();
        }
    },

    setAura: function(id, showAura) {
        $.each(this.tasks, function(key, task) {
            task.showAura = false;
        });
        if (this.tasks[id]) {
            this.tasks[id].showAura = showAura;
        }
    },

    setConnectionRule: function(sourceTaskId, targetTaskId, expression) {
        var source = this.tasks[sourceTaskId];

        // Get / create rule
        var rules = $.grep(source.rules, function(rule) {
            return rule.target == targetTaskId;
        });
        var rule = rules.length > 0 ? rules[0] : { target: targetTaskId };
        if (rules.length == 0) source.rules.push(rule);

        // Change rule (possible values: boolean/else)
        rule.expression = expression;

        // Publish event
        this.triggerModelChange();
    },

    /************************************************************************
     DELETE SECTION
     ************************************************************************/
    removeConnectorsInPredecessors: function(task, predecesor) {
        for (var i = 0; i < this.tasks[predecesor].connectors.length; i++) {
            if (this.tasks[predecesor].connectors[i].successor == task) {
                this.tasks[predecesor].connectors.splice(i, 1);
            }
        }
    },
    deleteTask: function(id) {
        var that = this;
        $.each(this.tasks, function(key, task) {
            // Remove from predecessors
            if (task.predecessors && task.predecessors.length > 0) {
                task.predecessors.removeAll(id);
                for (var i = 0; i < task.predecessors.length; i++) {
                    that.removeConnectorsInPredecessors(id, task.predecessors[i]);
                }
            }
            // Remove from successors
            if (task.successors && task.successors.length > 0) task.successors.removeAll(id);


        });

        // Remove task
        delete this.tasks[id];

        // Publish event
        this.triggerModelChange();
    },

    deleteArrow: function(sourceTaskId, targetTaskId) {
        var that = this;
        var source = this.tasks[sourceTaskId];
        var target = this.tasks[targetTaskId];

        if (source) source.successors.removeAll(targetTaskId);
        if (target) target.predecessors.removeAll(sourceTaskId);

        //Remove connectors
        that.removeConnectorsInPredecessors(targetTaskId, sourceTaskId);

        if (source.rules.length > 0) {
            for (var i = 0; i < source.rules.length; i++) {
                var rule = source.rules[i].target;
                if (source.successors.indexOf(rule) == -1) {
                    source.rules.splice(i, 1);
                }
            }
        }
        // Publish event
        this.triggerModelChange();
    },

    /************************************************************************
     MISC METHODS SECTION
     ************************************************************************/

    newGuid: function() {
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            var r = Math.random() * 16 | 0,
                v = c == 'x' ? r : (r & 0x3 | 0x8);
            return v.toString(16);
        });
    },

    getDefaultName: function(taskType) {
        var displaySecuence = 0;
        var numbersOfNameTask = [];
        var uniqueSecuence = [];
        var numberPattern = /\d+/g;
        var manualTasks = [];
        var key = 'Task';
        if(bizagi.bpmn.modeler.viewer.featureLocalization){
            key = bizagi.bpmn.modeler.viewer.getKeyLocalization("workportal-process-modeler-task");
        }
        if (taskType == "UserTask" || taskType == "EmailTask") {

            /* var manualTasks = $.grep(Object.values(this.tasks), function( task ) {
                 return task.type == "UserTask";
             });*/
            for (var task in this.tasks) {
                if (this.tasks[task].type === "UserTask" || this.tasks[task].type === "EmailTask") {
                    manualTasks.push(this.tasks[task])
                }
            }
            if (manualTasks.length > 0) {
                for (var i = 0; i < manualTasks.length; i++) {
                    if (manualTasks[i].displayName.indexOf(key) == 0) {
                        var secuence = manualTasks[i].displayName.match(numberPattern);
                        numbersOfNameTask.push(parseInt(secuence));
                    }
                }

                numbersOfNameTask = numbersOfNameTask.sort(function(a, b) {
                    return a - b;
                });

                /*
                $.each(numbersOfNameTask, function(i, el){
                    if($.inArray(el, uniqueSecuence) === -1 && el !=0 ) uniqueSecuence.push(el);
                });*/
                for (var i = 0; i < numbersOfNameTask.length; i++) {
                    if ($.inArray(numbersOfNameTask[i], uniqueSecuence) === -1 && numbersOfNameTask[i] != 0) uniqueSecuence.push(numbersOfNameTask[i]);
                }
            }

            for (var i = 1; i <= uniqueSecuence.length; i++) {
                displaySecuence = i;
                if (uniqueSecuence[i - 1] > i) {
                    displaySecuence = displaySecuence - 1;
                    break;
                }
            }
            return key + " " + (displaySecuence + 1);
        }
        return null;
    },

    //DR
    getDuration: function(taskType) {
        if (taskType == "UserTask") {
            return "1h";
        }
        return null;
    },
    //DR
    parseDuration: function(value) {
        if (String(value).indexOf("h") == -1 && String(value).indexOf("m") == -1) {
            value += "m";
            value = value.trim();
        }
        var regex = /(?:^\d+h[ ]+\d+m$)|(?:^\d+[hm]?$)/;
        if (typeof value != "string") {
            return value;
        } else {
            value = value.trim();
        }
        if (regex.test(String(value))) {
            var last_hour = "";
            var last_minuts = "";
            var resultMinuts = 0;
            var hour = value.indexOf("h");
            if (hour != -1) {
                last_hour = value.substring(0, hour);
                last_hour = Math.floor(last_hour * 60);
                value = value.split("h").pop();
                resultMinuts = last_hour;
            }
            var minuts = value.indexOf("m");
            if (minuts != -1) {
                last_minuts = value.substring(0, minuts);
                resultMinuts += parseInt(last_minuts);
            }
            return resultMinuts;
        } else {
            return null;
        }
    },
    formatDuration: function(value) {
        if (value != 0) {
            var m = value % 60;
            var h = (value - m) / 60;
            var hoursMinutes = h.toString() + "h " + m.toString() + "m";
            var result = hoursMinutes.replace(" 0m", "");
            if (h == 0){
                result = result.replace("0h", "");
            }
            return result;
        } else {
            return "0m";
        }
    },
    triggerModelRedraw: function() {
        this.publish("model.redraw");
    },

    triggerModelChange: function(params) {
        params = params || { redraw: true };
        this.publish("model.change");
        if (params.redraw) this.triggerModelRedraw();
    },
    triggerChangeShape: function(params) {
        this.publish("shape.change", params);
    },
    activeConfirmationPopup: function(item) {
        return ((item === "UserTask") || (item === "EmailTask") || (item === "IntermediateEvent") || (this.isGateway(item)))
    },
    //return if the element is editable name
    isEditableName: function(element) {
        return !((element.type === 'StartEvent') ||
            (element.type === 'EndEvent') ||
            (element.type === 'TerminateEvent')
        );
    },
    isEmailTask: function(type) {
        return type = "EmailTask";
    },
    isEvent: function(type) {
        return type == "ExclusiveGateway" || type == "IntermediateEvent";
    },
    isGateway: function(type) {
        return type == "ExclusiveGateway" || type == "ParallelGateway" || type == "InclusiveGateway" || type == "ComplexGateway";
    }
});